<template>
  <v-container fluid grid-list-md fill-height style="padding:0px 10px 5px 10px;width:100%;height:100%;">
    <v-layout row>
      <v-flex d-flex md2>
        <v-card> 
          <v-card-title  primary class="title" style="height:5%;width:100%;padding-top:5px;padding-bottom:5px;">Filters</v-card-title>
          <v-card-actions  fill-height style="height:95%;width:100%;padding-top:0px;padding-bottom:0px;align-items:flex-start;overflow:auto;"> 
            <v-expansion-panel>
              <v-expansion-panel-content 
                v-for="field in filterFields" :key="field">
                <template v-slot:header>
                  <div>{{field.title}}</div>
                </template>
                <v-checkbox
                  v-for="opt in field.options"
                  :key="opt"
                  :label="opt"
                  :value="opt"
                  v-model="field.model"
                  @change="filter"
                  style="padding-left:20px;"/>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-card-actions>
        </v-card> 
      </v-flex>
      <v-flex d-flex md10>
        <v-card> 
            <v-card-title  primary class="title" style="height:5%;width:100%;padding-top:5px;padding-bottom:5px;">Engine Types</v-card-title>
            <v-card-actions  fill-height style="height:95%;width:100%;padding-top:0px;padding-bottom:0px;"> 
              <dx-data-grid 
                class="grid"
                :data-source="customerGridFiltered"
                :show-column-lines="true"
                :show-row-lines="true"
                :show-borders="true"
                :word-wrap-enabled="true"
                :row-alternation-enabled="false"
                :column-auto-width="true"
                @cellClick="gridClick"
                @cellPrepared="cellPrepared">
                <dx-column
                  data-field="name"
                  caption="Customer"                
                />
                <dx-column
                  data-field="favorite"
                  cell-template="favTemplate"
                />
                <dx-column
                  data-field="tickets"
                  cell-template="ticketTemplate"
                  caption="Open Tickets"
                  alignment="left"
                />
                <dx-column
                  data-field="status"
                />
                <dx-column
                  data-field="engines"
                  cell-template="enginesTemplate"
                />
                <div
                  slot="favTemplate"
                  slot-scope="{ data }"
                  style="display:flex;justify-content:center;align-items:center;">
                  <img src="../assets/favorite.png" v-if="data.value">
                </div>
                <div
                  slot="ticketTemplate"
                  slot-scope="{ data }"
                  style="display:flex;align-items:center;">
                  <img src="../assets/ticket_dark.png" style="height:40px;width:40px;">
                  <div style="padding-left:15px;width:100%;height:100%;font-size:24px;">{{data.value}}</div>
                </div>
                <div
                  slot="enginesTemplate"
                  slot-scope="{ data }"
                  style="display:flex;align-items:center;">
                  <div v-for="eng in data.value" :key="eng" style="margin-right:30px;">
                    <v-layout row>
                      <img v-if="eng.color=='gray'" src="../assets/colordotsgray.png" style="height:20px;width:20px;margin:5px;">                        
                      <img v-else-if="eng.color=='yellow'" src="../assets/colordotsorange.png" style="height:20px;width:20px;margin:5px;"> 
                      <img v-else src="../assets/colordotsred.png" style="height:20px;width:20px;margin:5px;"> 
                      
                      <v-layout column>
                        <v-flex d-flex md6>
                          {{eng.engine}}
                        </v-flex>
                        <v-flex d-flex md6>
                          {{eng.vessel}}
                        </v-flex>
                      </v-layout>
                    </v-layout>                    
                  </div>
                </div>
              </dx-data-grid> 
            </v-card-actions>
          </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>


import { DxDataGrid, DxColumn, DxPaging, DxScrolling} from 'devextreme-vue/data-grid';

  export default {
    name: "CustomersOverview",
    components: {
      DxDataGrid,
      DxColumn,
      DxPaging,
      DxScrolling
    },

    data () {
      return {
        filterFields:[
          {
            title:'Control Unit',
            options:[],
            model:[]
          },
          {
            title:'Fuel Type',
            options:[],
            model:[]
          },
          {
            title:'Engine Type',
            options:['RT-flex58','X82','X92','X62','X72','RT-flex48','RT-flex60','RT-flex84','RT-flex96','X35','X40','RT-flex50DF','RT-flex50','RT-flex68','X52','X72 SCR','X62D','X72DF'],
            model:[]
          },
          {
            title:'Open Tickets',
            options:[],
            model:[]
          },
          {
            title:'Engine KPI',
            options:[],
            model:[]
          }
        ],
        customerGridFiltered:[],
        customerGridAll:[]
      }
    },
    props:
    {
      customersData:Array,
      active:Boolean
    },
    computed:
    {
    },
    created() {     

      this.customerGridFiltered = this.customerGridAll;
    },
    methods: {
      gridClick(e)
      {
        if ((e.column.dataField=="name")&&(e.rowType!="header"))
        {          
          this.$emit('compSelected',e.value);

        }
      },
      cellPrepared(e)
      {
        if ((e.column.dataField=="name")&&(e.rowType!="header"))
        {
          e.cellElement.classList.add('clickable');
        }

      },
      filter(filterValues)
      {
        if (filterValues.length==0) this.customerGridFiltered = this.customerGridAll;
        else
        {
          let tmp = [];
        
          for (let i=0;i<filterValues.length;i++)
          {
            tmp = tmp.concat(this.customerGridAll.filter(function (item) {

              let tmpEng =item.engines.map(function (eng) {
                return eng.engine;
              });   
              
              return tmpEng.includes(filterValues[i]);
            })
            );

          }

          this.customerGridFiltered = tmp;
        }     
        

      },
      setData()
      {
        let keys = Object.keys(this.customersData);

        for (let i=0;i<keys.length;i++)
        {
          console.log(this.customersData[keys[i]]);
        }
      }

     
    },
    watch:
    {
      customersData:function()
      {
        setTimeout(() => {
          this.setData();
        });   

      }    
    }

  }

</script>

