<template>
 <v-container fluid grid-list-md fill-height style="padding:0px 10px 5px 10px;width:100%;height:100%;">
    <v-layout row>
      <v-flex d-flex md6>
        <v-layout column fill-height style="overflow:auto;">
          <EngineCard v-for="cd in cardData" :key="cd"
            :cardData="cd">
          </EngineCard>    
        </v-layout>
      </v-flex>
      <v-flex d-flex md6>
        <v-card>
            <v-tabs v-model="tabActive" slider-color="rgb(144,170,218)" style="height:100%;width:100%;">
                <v-tab v-for="title in tabTitles" :key="title" ripple active-class='activeTab' style="text-transform:none;color:rgb(192,192,192);">
                    <span>{{title}}</span>
                </v-tab>
                <v-tab-item class="tabItem">
                    <OpenTickets/>
                </v-tab-item>
                <v-tab-item class="tabItem">
                    <ClosedTickets/>
                </v-tab-item>
            </v-tabs>
        </v-card> 
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import OpenTickets         from "./OpenTickets"
  import ClosedTickets  from "./ClosedTickets"
  import EngineCard from '../components/EngineCard'

  export default {
    name: "CompanyView",
    components: {
      EngineCard,
      OpenTickets,
      ClosedTickets
    },
    data () {
      return {
        tabActive:0,
        cardData:[],
        tabTitles: [
          'Open Tickets','Closed Tickets'
        ]
      }
    },
    props:
    {
      active:Boolean,
      companyData:Object
    },
    computed:
    {
    },
    created() {  
           
    },
    mounted() {    


    },
    methods: {
     
    },
    watch:
    {
      active:function()
      {
        this.cardData=[];

        let engines= this.companyData.engines;

        for (let i=0;i<engines.length;i++)
        {

          //bsfc
          let bsfcData={
            title:'SFOC',
            unit:'g/kWh',
            value: Math.random() * (220 - 150 + 1) + 150,
            format: 1,
            chartOptions:{
                time:{
                    useUTC:false
                },  
                chart:{ backgroundColor:'transparent'},
                tooltip:{
                    formatter: function () {
                        return this.points.reduce(function (s, point) {
                            return s + '<br/>' + point.series.name + ': ' + point.y.toFixed(2);
                        //}, '<b>' + this.x + '</b>');
                        }, '<b>' + new Date(this.x).toTimeString().substring(0,5) + '</b>');
                    },
                    shared:true
                },
                legend:{
                    enabled:false},
                title:{ text:undefined },
                xAxis: { 
                    type:'datetime',
                    labels:{ enabled:false},
                    crosshair:true
                },
                yAxis: {
                    labels:{ enabled:false },
                    title:{ text:undefined}},
                exporting:{
                    enabled : false
                },
                series: [{
                    name:'Values',
                    type:'line',
                    data:[],
                    marker:{
                        enabled:false
                    },
                    states: {
                        hover: {
                            enabled: false
                        },
                        normal:{
                        animation:false
                        },
                        inactive:{
                        opacity: 1
                        }
                    }
                },
                {
                    name:'Ref',
                    type:'line',
                    data:[],
                    marker:{
                        enabled:false
                    },
                    states: {
                        hover: {
                            enabled: false
                        },
                        normal:{
                        animation:false
                        },
                        inactive:{
                        opacity: 1
                        }
                    }
                }]
            }
          };

          for (let j=0;j<96;j++)
          {
            bsfcData.chartOptions.series[0].data.push([j, Math.random() * (220 - 150 + 1) + 150]);

            bsfcData.chartOptions.series[1].data.push([j, Math.random() * (220 - 150 + 1) + 150]);
          }

          //sloc
          let slocData={
            title:'SLOC',
            unit:'g/kWh',
            value: Math.random() * (1.3 - 0.9) + 0.9,
            format: 1,
            chartOptions: {
                chart:{ backgroundColor:'transparent'},                
                tooltip:{
                    formatter: function () {
                        return this.points.reduce(function (s, point) {
                            return s + '<br/>' + point.series.name + ': ' + point.y.toFixed(2);
                        }, '<b>' + new Date(this.x).toTimeString().substring(0,5) + '</b>');
                    },
                    shared:true
                },
                legend:{
                    enabled:false },
                title:{ text:undefined },
                xAxis: { 
                    labels:{ enabled:false},
                    crosshair:true
                },
                yAxis: {
                    labels:{ enabled:false },
                    title:{ text:undefined}},
                exporting:{
                    enabled : false
                },
                series: [{
                    name:'Values',
                    type:'line',
                    data:[],
                    marker:{
                        enabled:false
                    },
                    states: {
                        hover: {
                            enabled: false
                        },
                        normal:{
                        animation:false
                        },
                        inactive:{
                        opacity: 1
                        }
                    }
                }
                ]
            }
          };

          for (let j=0;j<96;j++)
          {
            slocData.chartOptions.series[0].data.push([j, Math.random() * (1.3 - 0.9) + 0.9]);
          }

          //fault
          let faultData=
          {
            name:'Fault '+Math.floor(Math.random()*12),
            date:new Date().toISOString().slice(0, 10),
            color:Math.round(Math.random()+1)*10,
            frequent:['CYL', 'FP','AC']
          };

          //tickets
          let ticketData =[];

          for (let j=0;j<24;j++)
          {

            ticketData.push({});
          }

          //kpi
          let kpi =  Math.floor(Math.random()*100);

          let color;

          if (kpi>80) color=0;
          else if (kpi>60) color=10;
          else color=20;

          this.cardData.push({
            name:engines[i].engine,
            lastUpdate:new Date().toISOString().slice(0, 10),
            vessel:engines[i].vessel,
            engineKPI:kpi,
            engineColor:color, 
            fault:faultData,
            bsfc:bsfcData,
            sloc:slocData,
            tickets:ticketData
          });

        }

      }
    }

  }

</script>

<style scoped>

.tabItem
{
  max-height: calc(100vh - 128px)
}


</style>

<style>

.activeTab
{
  color:rgb(144,170,218) !important;
  opacity:1 !important;
}

</style>
