              </v-flex>
              <v-flex d-flex md9>
                <v-card>
                  <Schematic
                    v-bind:schematicSource="schematicSource"
                    v-bind:points="points"
                    v-bind:initWidth="initWidth"
                    v-bind:initHeight="initHeight"
                    v-bind:active="active"/>
                  </v-card>
              </v-flex>              
            </v-layout>
          </v-flex>
