    },
    methods: {
     
    },
    watch:
    {
      selectedSystem:function()
      {      

        let selCompMap = this.compMapping[this.selectedSystem.replace(/  /g, ' ')];

        let keys = Object.keys(this.subSystemData);
        
        let engineTypes = keys.slice(0, keys.length-2);

        this.$set(this.gridData,'kpi',[]);

        for (let i=0;i<engineTypes.length;i++)
        {
          this.gridData['kpi'].push({
            engType:engineTypes[i],
            kpi:this.subSystemData[engineTypes[i]][selCompMap.tag+'_KPI']
          })
        }        
        this.$set(this.chartData,'compIssues',{
          chart:{
            height:0,
            zoomType: 'xy'
          },
          legend:{
            enabled:false
          },
          xAxis:{
            categories:selCompMap.components,          
            crosshair:true
          },
          yAxis:{
            title:
            {
              text:undefined
            }
          },
          exporting:{
            enabled : false
          },
          title:{
            text:undefined
          },
          series: [{
            type:'column',
            animation: false,
            data:[],
            states: {
              hover: {
                enabled: false
              },
              normal:{
                animation:false
              },
              inactive:{
                opacity: 1
              }
            }
          }]
        });
        
        for (let i=0;i<selCompMap.components.length;i++)
        {
          let num = 0;

          if (Object.keys(this.subSystemData.Components).includes(selCompMap.components[i]))
            num = this.subSystemData.Components[selCompMap.components[i]];         

          this.chartData['compIssues'].series[0].data.push({
            y:num,
            color:'rgb(144,170,218)'});
        }

        this.schematicSource = selCompMap.schematic;
        this.initWidth = selCompMap.initWidth;       
        this.initHeight = selCompMap.initHeight;

