import Vue from 'vue'
import Router from 'vue-router'
import VueCookies from 'vue-cookies'

import Customers from '../screens/Customers'
import Engines from '../screens/Engines'
import Subsystems from '../screens/Subsystems'
import Login from '../screens/Login'
//import Dashboard from '../../../EDSOnLine/src/screens/Dashboard'
// import CompanyView from '../screens/CompanyView'
// import CustomersOverview from '../screens/CustomersOverview'
// import EnginesOverview from '../screens/EnginesOverview'
// import EngineView from '../screens/EngineView'
// import SubsystemsOverview from '../screens/SubsystemsOverview'
// import SubsystemView from '../screens/SubsystemView'

import {store} from "../main.js"

Vue.use(Router);
Vue.use(VueCookies);

var router = new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      beforeEnter: (to, from, next) => {
          const { uri } = to.query;
          if (uri != null && uri != '/') {
              next(false);
              router.push(uri);
          } else {
              next();
          }
      },
      redirect: {
        name:"Login"
        //name: 'Engines'
       
      }
    },
    {
      path: '/customers',
      name: 'Customers',
      component: Customers
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/engines',
      name: 'Engines',
      component: Engines
    },
    {
      path: '/subsystems',
      name: 'Subsystems',
      component: Subsystems
    }
  ]
});

router.beforeEach((to, from, next) => {
  
  if ((to.path=='/login')||(store.userLogged)) {
    next()    
  }    
  else {
    console.log("go to login");
    next('/login')
  }        
})


export default router;




