<template>
  <v-container fluid fill-height grid-list-md style="padding:0px 10px 5px 10px;height:100%;width:100%;background-color:white;"> 
    <v-layout column fill-height style="height:100%;">
      <div style="width:100%;height:40px;display:flex;align-items:center;">
        <div v-for="(item,index) in breadCrumbList" v-bind:key="item" style="display:flex;align-items:center;">        
            <div class="titleBtn" v-if="index>0">></div>
            <v-btn flat class="titleBtn" v-if="item=='Engine View'" v-on:click="btnClick(item)">{{selEngine}}</v-btn>
            <v-btn flat class="titleBtn" v-else v-on:click="btnClick(item)">Overview</v-btn>
        </div>
      </div>
      <EnginesOverview v-show="screen=='Engines Overview'"
        class="tabItem"
        @engSelected="onEngSelect"
        v-bind:engTypeData="engTypeData"
        v-bind:active="screen=='Engines Overview'"/>
      <EngineView v-show="screen=='Engine View'" 
        class="tabItem"   
        @vesselSelected="onVessSelect"
        v-bind:active="screen=='Engine View'"
        v-bind:selEngine="selEngine"
        v-bind:engineData="engTypeData[selEngine]"/>      
    </v-layout>
  </v-container> 
</template>

<script>

  import EnginesOverview     from "./EnginesOverview"
  import EngineView  from "./EngineView"  
  import {store} from "../main.js"

  export default {
    name: "Engines",
    components: {
      EnginesOverview,
      EngineView
    },
    data () {
      return {
        screen:'',
        breadCrumbList:['Engines Overview'],
        selEngine:'',
        engTypeData:{}//{"X72": {"controlSystem": "UNIC", "companyNo": 1, "eng_KPI": 0.9997866560030122, "so_KPI": 1.0, "fi_KPI": 0.999659838052902, "sac_KPI": 1.0, "egs_KPI": 0.9983708959914089, "gas_KPI": 1.0, "pr_KPI": 0.9999671649469937, "ca_KPI": 0.8893312785575589, "bsfc": 187.68727895650065, "faultNo": 7, "soFaultNo": 0, "fiFaultNo": 0, "sacFaultNo": 0, "egsFaultNo": 0, "gasFaultNo": 0, "prFaultNo": 7, "common3faults": ["Scuffing"], "eng_KPI_color": 0, "so_KPI_color": 0, "fi_KPI_color": 0, "sac_KPI_color": 0, "egs_KPI_color": 0, "gas_KPI_color": 0, "pr_KPI_color": 0, "ca_KPI_color": 0}}
      //  engineTypes:['RT-flex58','X82','X92','X62','X72','RT-flex48','RT-flex60','RT-flex84','RT-flex96','X35','X40','RT-flex50DF','RTflex50','RTflex68','X52','X72 SCR','X62D','X72DF']
      }
    },
    computed: {
      dataLoaded: function () { 
        return store.type; }
    },
    created() {  
         
    },
    mounted() {
      setTimeout(() => {
        this.engTypeData =  store.engineAggr;   
      }); 

      this.screen ='Engines Overview';
    },
    methods: {   
      onEngSelect:function(eng)
      {
        this.selEngine = eng;
        this.breadCrumbList.push('Engine View');
        this.screen = 'Engine View';
      },
      onVessSelect:function(vessel)
      {
        window.open('http://localhost:8080');
      },
      btnClick:function(item)
      {
        if (this.breadCrumbList.includes(item))
        {
          let index = this.breadCrumbList.indexOf(item);

          this.breadCrumbList = this.breadCrumbList.slice(0,index+1);
        }
        else 
        {
          this.breadCrumbList.push(item);
        }

        this.screen=item;
      }
    },
    watch:
    {
      dataLoaded: function()
      {
        setTimeout(() => {
          this.engTypeData =  store.engineAggr; 
        }); 
      }
      
    }

  }

</script>

<style scoped>

.tabItem
{
  max-height: calc(100% - 40px)
}

</style>
