<template>
  <v-container fluid fill-height grid-list-md style="padding:0px 10px 5px 10px;height:100%;width:100%;background-color:white;"> 
    <v-layout column fill-height style="height:100%;">
      <div style="width:100%;height:40px;display:flex;align-items:center;">
        <div v-for="(item,index) in breadCrumbList" v-bind:key="item" style="display:flex;align-items:center;">        
            <div class="titleBtn" v-if="index>0">></div>
            <v-btn flat class="titleBtn" v-if="item=='Company View'" v-on:click="btnClick(item)">{{selCompany}}</v-btn>
            <v-btn flat class="titleBtn" v-else v-on:click="btnClick(item)">Overview</v-btn>
        </div>
        <v-spacer/>
        <v-checkbox         
          v-show="screen=='Company View'"
          v-model="favorite"
          label='Favorite'
          style="max-width:80px;"
          @change="favoriteChange"/>
        <img 
           v-show="screen=='Company View'"
           src="../assets/favorite.png">
      </div>
      <CustomersOverview v-show="screen=='Customers Overview'"
        class="tabItem"
        @compSelected="onCompSelect"
        v-bind:active="screen=='Customers Overview'"
        v-bind:customersData="customersData"/>
      <CompanyView v-show="screen=='Company View'" 
        class="tabItem"        
        v-bind:active="screen=='Company View'"
        v-bind:companyData="companyData"/>      
    </v-layout>
  </v-container> 
</template>

<script>

  import CustomersOverview     from "./CustomersOverview"
  import CompanyView  from "./CompanyView"
  import {store} from "../main.js"

  export default {
    name: "Customers",
    components: {
      CustomersOverview,
      CompanyView
    },
    data () {
      return {
        screen:'',
        breadCrumbList:['Customers Overview'],
        selCompany:'',
        engineTypes:['RT-flex58','X82','X92','X62','X72','RT-flex48','RT-flex60','RT-flex84','RT-flex96','X35','X40','RT-flex50DF','RT-flex50','RT-flex68','X52','X72 SCR','X62D','X72DF'], 
        customersData:[],
        companyData:{},
        favorite:false
      }
    },
    computed:
    {
      dataLoaded: function () { 
        return store.type; }
    },
    created() {     
      // for (let i=1;i<14;i++)
      // {
      //   var customer ={ name:'Shipping Company '+i };

      //   if (i%3==0) customer.favorite = true;
      //   else customer.favorite = false;

      //   customer.tickets = Math.floor(Math.random()*i);

      //   customer.status ='';

      //   customer.engines=[];

      //   let engNum = Math.floor(Math.random()*10);

      //   for (let j=0;j<engNum;j++)
      //   {
      //     let eng = this.engineTypes[Math.floor(Math.random()*(this.engineTypes.length-1))];

      //     let clr;

      //     if (j%3==0) clr= 'gray';
      //     else if (j%3==1) clr ='yellow';
      //     else clr = 'red';
        
      //     customer.engines.push({
      //       engine:eng,
      //       vessel:'Vessel '+i.toString()+j.toString(),
      //       color:clr});
      //   }

      //   this.customersData.push(customer);
          
      // }
    },
    mounted() {

      setTimeout(() => {
        this.customersData =  store.companyAggr;   
      }); 

      this.screen='Customers Overview';
    },
    methods: {
      onCompSelect:function(comp)
      {
        this.selCompany = comp;
        this.breadCrumbList.push('Company View');
        this.screen = 'Company View';
      },
      btnClick:function(item)
      {
        if (this.breadCrumbList.includes(item))
        {
          let index = this.breadCrumbList.indexOf(item);

          this.breadCrumbList = this.breadCrumbList.slice(0,index+1);
        }
        else 
        {
          this.breadCrumbList.push(item);
        }

        this.screen=item;
      }
     
    },
    watch:
    {
      selCompany:function()
      {
        let company = this.selCompany;

        this.companyData = this.customersData.filter(function (item) {
          return item.name == company;
        })[0];

        this.favorite = this.companyData.favorite;
      },
      favorite:function()
      {
        if (this.favorite!=this.companyData.favorite)
        {
          let company = this.selCompany;

          this.customersData.filter(function (item) {
          return item.name == company;})[0].favorite = this.favorite;

        }
      },
      dataLoaded: function()
      {
        setTimeout(() => {
          this.customersData =  store.companyAggr;   
        }); 
      }
    }

  }

</script>

<style scoped>

.tabItem
{
  max-height: calc(100% - 40px)
}
</style>
