<template>
   <v-container fluid fill-height grid-list-md style="padding:0px 10px 5px 10px;height:100%;width:100%;background-color:white;"> 
    <v-layout column fill-height style="height:100%;">
      <div style="width:100%;height:40px;display:flex;align-items:center;">
        <div v-for="(item,index) in breadCrumbList" v-bind:key="item" style="display:flex;align-items:center;">        
            <div class="titleBtn" v-if="index>0">></div>
            <v-btn flat class="titleBtn" v-if="item=='Subsystem View'" v-on:click="btnClick(item)">{{selSubSys}}</v-btn>
            <v-btn flat class="titleBtn" v-else v-on:click="btnClick(item)">Overview</v-btn>
        </div>
      </div>
      <SubsystemsOverview v-show="screen=='Subsystems Overview'"
        class="tabItem"
        @sysSelected="onSysSelect"
        v-bind:subSysData="subSysData"
        v-bind:active="screen=='Subsystems Overview'"/>
      <SubsystemView v-show="screen=='Subsystem View'" 
        class="tabItem"        
        v-bind:active="screen=='Subsystem View'"
        v-bind:subSystemData="subSysData[selSubSys]"
        v-bind:selectedSystem="selSubSys"/>      
    </v-layout>
  </v-container> 
</template>

<script>

  import SubsystemsOverview     from "./SubsystemsOverview"
  import SubsystemView  from "./SubsystemView"  
  import {store} from "../main.js"

  export default {
    name: "Subsystems",
    components: {
      SubsystemsOverview,
      SubsystemView
    },
    data () {
      return {        
        screen:'',
        breadCrumbList:['Subsystems Overview'],
        selSubSys:'',
        subSysData:{}// {"Servo Oil": {"X72": {"so_KPI": 1.0}, "soFaultNo": 0, "Components": {}}, "Fuel Injection": {"X72": {"fi_KPI": 0.999659838052902}, "fiFaultNo": 0, "Components": {}}, "Scavenge Air": {"X72": {"sac_KPI": 1.0}, "sacFaultNo": 0, "Components": {}}, "Exhaust Gas": {"X72": {"egs_KPI": 0.9983708959914089}, "egsFaultNo": 0, "Components": {}}, "Piston Running": {"X72": {"pr_KPI": 0.9999671649469937}, "prFaultNo": 7, "Components": {"Cylinder Liner": 7}}, "Gas Admission": {"X72": {"gas_KPI": 1.0}, "gasFaultNo": 0, "Components": {}}}
      }
    },
    computed: {
      dataLoaded: function () { 
        return store.type; }
    },
    created() {     
    },
    mounted() {

      setTimeout(() => {
        this.subSysData =  store.subSystemAggr;   
      }); 

      this.screen ='Subsystems Overview';
    },
    methods: {
      onSysSelect:function(sys)
      {
        this.selSubSys = sys;
        this.breadCrumbList.push('Subsystem View');
        this.screen = 'Subsystem View';
      },
      btnClick:function(item)
      {
        if (this.breadCrumbList.includes(item))
        {
          let index = this.breadCrumbList.indexOf(item);

          this.breadCrumbList = this.breadCrumbList.slice(0,index+1);
        }
        else 
        {
          this.breadCrumbList.push(item);
        }

        this.screen=item;
      }
     
    },
    watch:
    {
      dataLoaded: function()
      {
        setTimeout(() => {
          this.subSysData =  store.subSystemAggr; 
        }); 
      }
    }

  }

</script>

<style scoped>

.tabItem
{
  max-height: calc(100% - 40px)
}

</style>
